<template>
  <div class="row" v-if="isReady">
    <!-- check data and set IsReady by getter methods -->

    <div class="col-12 flex-x-between mt-4">
      <div class="flex-align-center">
        <h3 class="my-0">Settings</h3>
      </div>

      <div class="flex-align-center flex-end"></div>
    </div>

    <div class="col-12 mt-3">
      <div class="row mx-0 box">
        <div
          class="col-lg-4 col-md-4 setting-box pointer"
          v-for="setting in settings"
          :key="setting.name"
          @click="$router.push(setting.route)"
        >
          <div class="d-flex align-items-start setting-box-content">
            <i
              :class="setting.icon"
              class="mr-3"
              style="font-size: 22px !important"
            ></i>
            <div>
              <p class="mt-0 primary font-weight-bold mb-1">
                {{ setting.name }}
              </p>
              <p class="gray normal-text">{{ setting.info }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";

export default {
  data() {
    return {};
  },

  computed: {
    ...mapState(["store", "isReady"]),
    ...mapGetters(["isStoreRead", "checkIsReady", "isLocationRead"]),
    settings() {
      var plan = this.store?.plan?.plan.name;
      var settings = [
        {
          name: "General",
          icon: "bx bxs-cog",
          info: "View and update your store details.",
          route: "/settings/general",
          isShow: false,
        },
        {
          name: "Cargo Or Supplier",
          icon: "bx bxs-truck ",
          info: "Save your cargo lists.",
          route: "/settings/suppliers",
          isShow: false,
        },
        {
          name: "Deliveries",
          icon: "bx bx-cycling",
          info: "Save your deliveries lists.",
          route: "/settings/deliveries",
          isShow: true,
        },
        {
          name: "Customers",
          icon: "bx bx-group",
          info: "Save your customers lists.",
          route: "/settings/customers",
          isShow: false,
        },
        {
          name: "Media library",
          icon: "bx bxs-image",
          info: "Manage your media lists.",
          route: "/settings/media",
          isShow: true,
        },
        {
          name: "Membership plan",
          icon: "bx bxs-award",
          info: `Current plan: ${plan}`,
          route: "/settings/plan",
          isShow: false,
        },
        {
          name: "My Account",
          icon: "bx bx-user",
          info: "Manage your account.",
          route: "/settings/my-account",
          isShow: true,
        },
      ];
      return settings.filter((data) => data.isShow);
    },
  },
  components: {},
  mounted() {
    this.$store.commit("setIsReady", true);
  },
  methods: {},
};
</script>
<style>
.setting-box {
  padding: 15px;
  border-radius: 0.85rem;
}
.setting-box:hover {
  background: rgba(var(--gray-2));
}

.setting-box:hover i {
  color: rgba(var(--primary), 1);
  background: rgba(var(--primary), 0.2);
}
.setting-box-content i {
  border-radius: 0.6rem;
  padding: 15px;
  background: rgba(var(--dark), 0.03);
}
</style>